.site-footer {
  background-color: #2c3e50;
  color: white;
  padding: 2rem 0;
  margin-top: 3rem;
  border-top: 1px solid #eaeaea;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.footer-logo {
  margin-bottom: 1.5rem;
}

.footer-logo-img {
  max-width: 150px;
  height: auto;
  filter: brightness(0) invert(1);
}

.footer-info {
  text-align: center;
}

.footer-info p {
  margin: 0.5rem 0;
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .site-footer {
    padding: 1.5rem 0;
  }
  
  .footer-logo-img {
    max-width: 120px;
  }
}
