.site-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem 0;
  border-bottom: 1px solid #eaeaea;
  margin-bottom: 2rem;
  background-color: white;
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.5rem;
}

.header-logo {
  max-width: 200px;
  height: auto;
  margin-bottom: 0.5rem;
}

.logo-container h2 {
  color: #7f8c8d;
  font-weight: normal;
  font-size: 1.2rem;
  margin: 0;
}

.main-nav ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 1rem;
}

.main-nav button {
  background: none;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #2c3e50;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.main-nav button:hover {
  background-color: #f5f5f5;
}

.main-nav button.active {
  background-color: #3498db;
  color: white;
}

@media (max-width: 768px) {
  .main-nav ul {
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
  }
  
  .main-nav button {
    width: 100%;
    text-align: center;
  }
}
